.App {
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  max-width: 800px; 
  background-color: floralwhite ;
  overflow: auto;
  /* min-width: 640px; */
}
.App.dark {
  background-color: black;
}
.AppWrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: cornsilk;
}
.AppWrap.dark {
  background-color: #444;
}
